// --- CHATBOT ---

// Get chat history for a subject
protectedRoutes.get('/chat/:subjectId/history', async (c) => {
    try {
        const userId = getUserId(c);
        const subjectId = c.req.param('subjectId');

        // Verify ownership
        const materia = await c.env.DB.prepare('SELECT id FROM materias WHERE id = ? AND user_id = ?').bind(subjectId, userId).first();
        if (!materia) {
            return c.json({ error: 'Materia no encontrada' }, 404);
        }

        // Fetch chat history
        const { results } = await c.env.DB.prepare(`
            SELECT rol, mensaje, created_at 
            FROM chat_historial 
            WHERE user_id = ? AND materia_id = ?
            ORDER BY created_at ASC
        `).bind(userId, subjectId).all();

        return c.json(results || []);
    } catch (error) {
        console.error('Chat history error:', error);
        return c.json({ error: error.message }, 500);
    }
});

// Clear chat history for a subject
protectedRoutes.delete('/chat/:subjectId/history', async (c) => {
    try {
        const userId = getUserId(c);
        const subjectId = c.req.param('subjectId');

        await c.env.DB.prepare('DELETE FROM chat_historial WHERE user_id = ? AND materia_id = ?').bind(userId, subjectId).run();

        return c.json({ message: 'Historial eliminado' });
    } catch (error) {
        console.error('Clear history error:', error);
        return c.json({ error: error.message }, 500);
    }
});

protectedRoutes.post('/chat/:subjectId/ask', async (c) => {
    try {
        const userId = getUserId(c);
        const subjectId = c.req.param('subjectId');
        const { question } = await c.req.json();

        if (!question) {
            return c.json({ error: 'La pregunta es requerida' }, 400);
        }

        // 1. Verify ownership and get Subject details
        const materia = await c.env.DB.prepare('SELECT * FROM materias WHERE id = ? AND user_id = ?').bind(subjectId, userId).first();

        if (!materia) {
            return c.json({ error: 'Materia no encontrada' }, 404);
        }

        // 2. Fetch Syllabus (Units + Topics)
        const { results: unidades } = await c.env.DB.prepare(`
        SELECT u.nombre as unidad, t.nombre as tema, t.descripcion 
        FROM syllabus_unidades u
        LEFT JOIN syllabus_temas t ON u.id = t.unidad_id
        WHERE u.materia_id = ?
        ORDER BY u.orden, t.orden
      `).bind(subjectId).all();

        // Format Syllabus Context
        let syllabusContext = "SYLLABUS:\n";
        let currentUnit = null;
        if (unidades && unidades.length > 0) {
            unidades.forEach(row => {
                if (row.unidad !== currentUnit) {
                    syllabusContext += `\nUnidad: ${row.unidad}\n`;
                    currentUnit = row.unidad;
                }
                if (row.tema) {
                    syllabusContext += `- ${row.tema}: ${row.descripcion || ''}\n`;
                }
            });
        } else {
            if (materia.syllabus) {
                syllabusContext += materia.syllabus;
            } else {
                syllabusContext += "No hay syllabus detallado disponible.\n";
            }
        }

        // 3. Fetch Notes
        const { results: apuntes } = await c.env.DB.prepare('SELECT titulo, contenido FROM apuntes WHERE materia_id = ?').bind(subjectId).all();

        let notesContext = "\nAPUNTES (Tus notas):\n";
        if (!apuntes || apuntes.length === 0) {
            notesContext += "No hay apuntes registrados para esta materia.\n";
        } else {
            apuntes.forEach(note => {
                notesContext += `\nTitulo: ${note.titulo}\n`;
                if (note.contenido) notesContext += `Contenido: ${note.contenido}\n`;
            });
        }

        const fullContext = `
        Materia: ${materia.nombre}
        Descripcion: ${materia.descripcion}
        
        ${syllabusContext}
        
        ${notesContext}
      `;

        // 4. Generate AI Response using Cloudflare Workers AI
        if (!c.env.AI) {
            return c.json({ error: 'Configuration Error: AI binding is missing.' }, 500);
        }

        const aiResponse = await generateResponse(fullContext, question, c.env.AI);

        // 5. Save chat messages to history
        await c.env.DB.prepare(
            'INSERT INTO chat_historial (user_id, materia_id, rol, mensaje) VALUES (?, ?, ?, ?)'
        ).bind(userId, subjectId, 'user', question).run();

        await c.env.DB.prepare(
            'INSERT INTO chat_historial (user_id, materia_id, rol, mensaje) VALUES (?, ?, ?, ?)'
        ).bind(userId, subjectId, 'assistant', aiResponse).run();

        return c.json({ answer: aiResponse });

    } catch (error) {
        console.error('Chatbot API error:', error);
        return c.json({ error: error.message || 'Error interno del servidor' }, 500);
    }
});

// --- CALENDARIO ---
protectedRoutes.get('/calendario', async (c) => {
    const userId = getUserId(c);
    const tipo = c.req.query('tipo');

    let query = `
    SELECT e.*, m.nombre as materia_nombre, m.color as materia_color
    FROM eventos_calendario e
    LEFT JOIN materias m ON e.materia_id = m.id
    WHERE e.user_id = ?
  `;

    const params = [userId];
    if (tipo) {
        query += ` AND e.tipo = ?`;
        params.push(tipo);
    }

    query += ' ORDER BY e.fecha_inicio';

    const { results } = await c.env.DB.prepare(query).bind(...params).all();
    return c.json(results);
});

protectedRoutes.post('/calendario', async (c) => {
    const userId = getUserId(c);
    const { materia_id, titulo, descripcion, fecha_inicio, fecha_fin, tipo } = await c.req.json();

    const result = await c.env.DB.prepare(
        `INSERT INTO eventos_calendario (user_id, materia_id, titulo, descripcion, fecha_inicio, fecha_fin, tipo)
     VALUES (?, ?, ?, ?, ?, ?, ?)`
    ).bind(userId, materia_id || null, titulo, descripcion || null, fecha_inicio, fecha_fin || null, tipo || 'estudio').run();

    const evento = await c.env.DB.prepare('SELECT * FROM eventos_calendario WHERE id = ?').bind(result.meta.last_row_id).first();

    if (!evento) {
        // Fallback if select fails
        return c.json({
            id: result.meta.last_row_id,
            user_id: userId,
            materia_id: materia_id || null,
            titulo,
            descripcion: descripcion || null,
            fecha_inicio,
            fecha_fin: fecha_fin || null,
            tipo: tipo || 'estudio',
            completado: 0
        }, 201);
    }

    return c.json(evento, 201);
});

protectedRoutes.put('/calendario/:id', async (c) => {
    const userId = getUserId(c);
    const id = c.req.param('id');
    const body = await c.req.json();

    // Build dynamic update query
    const updates = [];
    const params = [];

    if (body.titulo !== undefined) { updates.push('titulo = ?'); params.push(body.titulo); }
    if (body.descripcion !== undefined) { updates.push('descripcion = ?'); params.push(body.descripcion); }
    if (body.fecha_inicio !== undefined) { updates.push('fecha_inicio = ?'); params.push(body.fecha_inicio); }
    if (body.fecha_fin !== undefined) { updates.push('fecha_fin = ?'); params.push(body.fecha_fin); }
    if (body.tipo !== undefined) { updates.push('tipo = ?'); params.push(body.tipo); }
    if (body.completado !== undefined) { updates.push('completado = ?'); params.push(body.completado ? 1 : 0); }
    if (body.materia_id !== undefined) { updates.push('materia_id = ?'); params.push(body.materia_id); }

    if (updates.length === 0) {
        return c.json({ error: 'No hay campos para actualizar' }, 400);
    }

    params.push(id, userId);

    const query = `UPDATE eventos_calendario SET ${updates.join(', ')} WHERE id = ? AND user_id = ?`;

    await c.env.DB.prepare(query).bind(...params).run();

    const evento = await c.env.DB.prepare('SELECT * FROM eventos_calendario WHERE id = ?').bind(id).first();
    if (!evento) return c.json({ error: 'Evento no encontrado' }, 404);
    return c.json(evento);
});

protectedRoutes.patch('/calendario/:id/toggle', async (c) => {
    const userId = getUserId(c);
    const id = c.req.param('id');

    const evento = await c.env.DB.prepare('SELECT * FROM eventos_calendario WHERE id = ? AND user_id = ?').bind(id, userId).first();
    if (!evento) return c.json({ error: 'Evento no encontrado' }, 404);

    const nuevoEstado = evento.completado ? 0 : 1;

    await c.env.DB.prepare('UPDATE eventos_calendario SET completado = ? WHERE id = ?').bind(nuevoEstado, id).run();

    return c.json({ ...evento, completado: nuevoEstado });
});

protectedRoutes.delete('/calendario/:id', async (c) => {
    const userId = getUserId(c);
    const id = c.req.param('id');

    const result = await c.env.DB.prepare('DELETE FROM eventos_calendario WHERE id = ? AND user_id = ?').bind(id, userId).run();
    if (result.meta.changes === 0) return c.json({ error: 'Evento no encontrado' }, 404);
    return c.json({ message: 'Evento eliminado' });
});

